"use client";
import { useState, useEffect } from "react";

const WeatherWidget = () => {
  const [weatherData, setWeatherData] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchWeather = async () => {
      try {
        const res = await fetch("http://localhost:5000/weatherData/weather/current");
        const data = await res.json();
        setWeatherData(data);
      } catch (err) {
        console.error("Error fetching weather:", err);
      } finally {
        setLoading(false);
      }
    };

    fetchWeather();
    const interval = setInterval(fetchWeather, 60 * 60 * 1000);
    return () => clearInterval(interval);
  }, []);

  if (loading) {
    return (
      <div className="weather-widget-loading text-center text-white p-5"
        style={{
          background: "linear-gradient(135deg, #1a3a8f 0%, #4a6fa5 100%)",
          borderRadius: "20px",
          minHeight: "250px",
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          alignItems: "center"
        }}
      >
        <h5 className="mt-3">Loading weather data...</h5>
        <div className="spinner-border text-light mt-2" role="status">
          <span className="visually-hidden">Loading...</span>
        </div>
      </div>
    );
  }

  if (!weatherData) {
    return (
      <div className="weather-widget-error text-center text-white p-5"
        style={{
          background: "linear-gradient(135deg, #8f1a1a 0%, #a54a4a 100%)",
          borderRadius: "20px",
          minHeight: "250px",
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          alignItems: "center"
        }}
      >
        <div className="error-icon mb-3" style={{ fontSize: "3rem" }}>‚ö†Ô∏è</div>
        <h5>Unable to load weather data</h5>
        <p className="mb-0">Please try again later</p>
      </div>
    );
  }

  return (
    <div
      className="text-white py-5 px-4"
      style={{
        backgroundImage: "url('/souda.jpg')",
        backgroundSize: "cover",
        backgroundPosition: "center",
        borderRadius: "12px",
      }}
    >
      <div className="container">
        <div className="row align-items-center">
          {/* Left section (main info) */}
          <div className="col-md-4 text-center">
            <div style={{ fontSize: "3rem" }}>{weatherData.icon}</div>
            <h1 className="fw-bold display-3">{weatherData.temperature}¬∞C</h1>
            <p className="fs-5">{weatherData.condition}</p>
            <p className="mb-0">{weatherData.location}</p>
            <small>{new Date(weatherData.timestamp).toLocaleDateString()}</small>
          </div>

          {/* Right section (details) */}
          <div className="col-md-8">
            <div className="row text-center">
              <div className="col-md-3 col-6 mb-3">
                <h6>üíß Humidity</h6>
                <p className="fs-5 fw-bold">{weatherData.humidity}%</p>
              </div>
              <div className="col-md-3 col-6 mb-3">
                <h6>üå´Ô∏è Wind</h6>
                <p className="fs-5 fw-bold">{weatherData.windSpeed} km/h</p>
              </div>
              <div className="col-md-3 col-6 mb-3">
                <h6>‚òÅÔ∏è Condition</h6>
                <p className="fs-5 fw-bold">{weatherData.description}</p>
              </div>
              <div className="col-md-3 col-6 mb-3">
                <h6>Updated</h6>
                <p className="fs-6">{new Date(weatherData.timestamp).toLocaleTimeString()}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default WeatherWidget;
